import Card from "../components/Card";
import { useAppDispatch, useAppSelector } from "../redux/hooks";
import {
  deleteToCart,
  increaseToCart,
  decreaseToCart,
} from "../redux/features/ShoppingSlice";
import { Outlet, useNavigate } from "react-router-dom";

const Basket = () => {
  const productItem = useAppSelector((state) => state.shop.productsInCart);
  const productCount = useAppSelector((state) => state.shop.totalProductCount);
  const dispatch = useAppDispatch();
  const navigate = useNavigate();

  const totalPriceToCart = () => {
    let totalPrice = 0;
    productItem.forEach((element) => {
      totalPrice += element.amount * element.price;
    });
    return totalPrice.toFixed(2);
  };

  const isCartEmpty = () => {
    return productItem.length === 0;
  };
  return (
    <div>
      <div className="bg-gray-700 h-16 pt-4">
        <h2 className="text-center text-white font-light text-2xl">
          My Basket
        </h2>
      </div>

      <div className="mt-12 mx-4 grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-4 gap-x-4 gap-y-8">
        {productItem.map((urun, index) => (
          <Card
            incDecButton={true}
            boxClassName="relative border-gray-300 mx-auto p-2 sm:p-10 h-full w-full border border-double rounded-tl-lg rounded-br-lg shadow-lg"
            buttonClassName="absolute top-2 right-2 cursor-pointer font-thin text-sm text-white bg-gray-700 w-8 h-8 rounded-full"
            onClick={() => dispatch(deleteToCart(urun))}
            buttonText="X"
            key={index}
            item={urun}
            incOnClick={() => dispatch(increaseToCart(urun))}
            decOnClick={() => {
              if (urun.amount === 1) {
                dispatch(deleteToCart(urun));

                return;
              }
              dispatch(decreaseToCart(urun));
            }}
          />
        ))}
      </div>
      {isCartEmpty() ? (
        "Basket is empty."
      ) : (
        <div className="relative">
          <h6 className="absolute mt-8 right-52 text-lg  text-gray-900 font-bold">
            Total Price: {totalPriceToCart()}$
          </h6>
          <button
            className="absolute right-8 mt-8 bg-gray-700 rounded w-36 h-10 text-white font-light"
            onClick={() => navigate("/Form")}
          >
            Pay
          </button>
          <Outlet />
        </div>
      )}
      <div className="my-16 relative">
        <button
          className="absolute left-8 bg-gray-700 rounded w-36 h-10 text-white font-light"
          onClick={() => navigate("/")}
        >
          Back to shopping
        </button>
        <Outlet />
      </div>
    </div>
  );
};

export default Basket;
