import { Outlet, useNavigate } from "react-router-dom";
import { useAppSelector } from "../redux/hooks";
import { RiShoppingBasket2Line } from "react-icons/ri";
import { AiFillHeart } from "react-icons/ai";

const Header = () => {
  const shop = require("../styles/img/svg/shopbasket.png") as string;
  const heart = require("../styles/img/svg/heart.jpg") as string;
  const logo = require("../styles/img/svg/logo.png") as string;
  const products = useAppSelector((state) => state.shop.productsInCart);
  const fav = useAppSelector((state) => state.shop.favItems);

  const navigate = useNavigate();
  return (
    <div className="flex justify-between mx-auto bg-gray-50">
      <div className="w-48 h-24">
        <img src={logo} alt="" />
      </div>
      <div className="flex justify-between mx-8">
        <div className="flex cursor-pointer items-center my-3">
          <RiShoppingBasket2Line
            onClick={() => navigate("/Basket")}
            className="w-9 h-9"
          />
          <Outlet />

          {products.length > 0 && (
            <h2 className="text-gray-800 font-semibold mx-1 mt-2">
              {products.length}
            </h2>
          )}
        </div>
        <div className="flex cursor-pointer items-center my-3">
          <AiFillHeart
            className="w-7 h-7 cursor-pointer mt-2 mx-1"
            onClick={() => navigate("/Favorites")}
          />
          <Outlet />

          {fav.length > 0 && (
            <h2 className="text-gray-800 font-semibold mx-1 mt-2">
              {fav.length}
            </h2>
          )}
        </div>
      </div>
    </div>
  );
};
export default Header;
