import React, { useEffect } from "react";
import Card from "../components/Card";
import { useAppSelector, useAppDispatch } from "../redux/hooks";
import {
  addProducts,
  addToCart,
  favAddToCart,
} from "../redux/features/ShoppingSlice";

const ProductList = () => {
  const shop = useAppSelector((state) => state.shop);
  const dispatch = useAppDispatch();

  useEffect(() => {
    if (shop.allProducts.length < 1) {
      fetch("https://fakestoreapi.com/products")
        .then((res) => res.json())
        .then((res) => dispatch(addProducts(res)));
    }
  }, []);

  return (
    <div className="grid grid-cols-1 sm:grid-cols-2 md:grid-cols-4 gap-x-2 gap-y-8 bg-gray-50">
      {shop.allProducts.map((product, index) => (
        <Card
          onClick={() => dispatch(addToCart(product))}
          favOnClick={() => {
            dispatch(favAddToCart(product));
          }}
          buttonText="Add to Basket"
          key={index}
          item={product}
          boxClassName="mx-auto sm:p-10 border border-gray-300 border-double rounded-tl-lg rounded-br-lg shadow-lg"
        />
      ))}
    </div>
  );
};

export default ProductList;
