import React from "react";
import Card from "../components/Card";
import { useAppSelector, useAppDispatch } from "../redux/hooks";
import { deleteToCart } from "../redux/features/ShoppingSlice";
import { Outlet, useNavigate } from "react-router-dom";

const Favorites = () => {
  const favItem = useAppSelector((state) => state.shop.favItems);
  const dispatch = useAppDispatch();
  const navigate = useNavigate();

  const isFavoritesEmpty = () => {
    return favItem.length === 0;
  };

  return (
    <div>
      <div className="bg-gray-700">
        <h2 className="text-center pt-4 text-white font-light text-2xl h-16">
          My Favorites
        </h2>
      </div>
      <div className="mt-12 mx-4 grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-4 gap-x-4 gap-y-8">
        {favItem.map((urun, index) => (
          <Card
            boxClassName="relative border-gray-300 mx-auto p-5 sm:p-10 h-full w-full border border-double rounded-tl-lg rounded-br-lg shadow-lg"
            key={index}
            item={urun}
            buttonClassName="absolute right-6 top-8 cursor-pointer font-thin text-sm text-white bg-gray-600 w-6 h-6 rounded-full"
            onClick={() => dispatch(deleteToCart(urun))}
            buttonText="X"
          />
        ))}
      </div>
      {isFavoritesEmpty() ? "Favorites empty." : ""}
      <div className="my-16 relative">
        <button
          className="absolute right-4 bg-gray-700 rounded w-36 h-12 text-white font-light"
          onClick={() => navigate("/")}
        >
          Back to Shopping
        </button>
        <Outlet />
      </div>
    </div>
  );
};

export default Favorites;
