import React, { useState } from "react";
import { useForm } from "react-hook-form";
import { Outlet, useNavigate } from "react-router-dom";
import { useAppSelector } from "../redux/hooks";

interface FormData {
  name: string;
  email: string;
  cardNumber: string;
  securityCode: string;
  adress: string;
}

const Form: React.FC = () => {
  const [formData, setFormData] = useState<FormData>({
    name: "",
    email: "",
    cardNumber: "",
    securityCode: "",
    adress: "",
  });
  const { register } = useForm<FormData>();

  const handleChange = (event: React.ChangeEvent<HTMLInputElement>) => {
    const { name, value } = event.target;
    setFormData({ ...formData, [name]: value });
  };

  const handleSubmit = (event: React.FormEvent<HTMLFormElement>) => {
    console.log(formData);
  };
  const navigate = useNavigate();
  const productItem = useAppSelector((state) => state.shop.productsInCart);

  const totalPriceToCart = () => {
    let totalPrice = 0;
    productItem.forEach((element) => {
      totalPrice += element.amount * element.price;
    });
    return totalPrice.toFixed(2);
  };

  return (
    <div>
      <div className="bg-gray-700">
        <h2 className="text-center pt-4 text-white font-light text-2xl h-16">
          Pay
        </h2>
      </div>
      <div className="w-full sm:w-1/2 h-full mt-28 mx-auto">
        <form
          onSubmit={handleSubmit}
          className="grid grid-cols-1 sm:grid-cols-2 px-4 gap-x-3"
        >
          <div>
            <h1 className="text-2xl font-semibold text-black">Adress nfo</h1>
            <br />
            <label>
              Email:
              <input
                type="email"
                name="email"
                value={formData.email}
                onChange={handleChange}
                className="border-gray-200 border-solid border-2 ml-6 mb-4"
              />
            </label>
            <br />
            <label>
              Adress:
              <textarea
                name="postContent"
                maxLength={40}
                className="border-gray-200 border-solid border-2 ml-4"
              />
            </label>
          </div>
          <div>
            <h1 className="text-2xl font-semibold text-black">Payment</h1>
            <br />
            <label className="mb-6">
              Card Number :
              <input
                type="number"
                value={formData.cardNumber}
                {...register("cardNumber")}
                onChange={handleChange}
                className="border-gray-200 border-solid border-2 ml-6 mb-4"
              />
            </label>
            <br />
            <label>
              Security Code :
              <input
                type="securityCode"
                name="securityCode"
                value={formData.securityCode}
                onChange={handleChange}
                className="border-gray-200 border-solid border-2 ml-6 mb-4"
              />
            </label>

            <div className="relative">
              <h6 className="absolute top-10 right-24 text-gray-900 font-semibold">
                Total Price: {totalPriceToCart()}$
              </h6>
            </div>
            <button
              type="submit"
              className="bg-gray-900 text-white w-16 h-8 mt-8 rounded ml-64 mb-4"
            >
              Pay
            </button>
          </div>
        </form>

        <button
          type="submit"
          className="bg-gray-900 text-white w-36 h-8 rounded"
          onClick={() => navigate("/Basket")}
        >
          Back to basket
        </button>
        <Outlet />
      </div>
    </div>
  );
};

export default Form;
