import React, { FC, useState } from "react";
import { IProduct } from "../redux/features/ShoppingSlice";
import {
  AiFillPlusSquare,
  AiFillMinusSquare,
  AiOutlineHeart,
  AiFillHeart,
} from "react-icons/ai";

interface CardProps {
  item: IProduct;
  buttonText?: string;
  onClick?: () => void;
  boxClassName?: string;
  buttonClassName?: string;
  incDecButton?: boolean;
  incOnClick?: () => void;
  decOnClick?: () => void;
  favOnClick?: () => void;
}

const Card: FC<CardProps> = ({
  item,
  buttonText,
  onClick,
  boxClassName = "relative border-double rounded-tl-lg rounded-br-lg shadow-lg",
  buttonClassName = "cursor-pointer font-thin text-sm text-white bg-gray-700 w-28 h-8 rounded",
  incDecButton = false,
  incOnClick,
  decOnClick,
  favOnClick,
}) => {
  const [isFaved, setisFaved] = useState(false);
  return (
    <div className="container cursor-pointer px-3 object-cover hover:scale-110">
      <div className={boxClassName}>
        <div
          onClick={() => {
            setisFaved(!isFaved);
            return favOnClick ? favOnClick() : null;
          }}
          className="cursor-pointer w-5"
        >
          {isFaved ? <AiFillHeart size={20} /> : <AiOutlineHeart size={20} />}
        </div>
        {buttonText === "Delete" ? (
          <button onClick={onClick} className={buttonClassName}>
            {buttonText}
          </button>
        ) : null}
        <img
          className="w-48 h-32 object-scale-down mx-auto"
          src={item.image}
          alt=""
        />
        <div className="text-center text font-medium px-4 line-clamp-1 md:text-clip my-1 text-gray-600">
          <h1>{item.title}</h1>
        </div>
        <div className="flex justify-evenly">
          {incDecButton ? (
            <div>
              <AiFillPlusSquare onClick={incOnClick} size={24} />
            </div>
          ) : null}
          {item.amount ? <h2>{item.amount}</h2> : null}
          {incDecButton ? (
            <div>
              <AiFillMinusSquare onClick={decOnClick} size={24} />
            </div>
          ) : null}
          <label className="font-bold text-lg price text-gray-900">
            {item.price}$
          </label>
          {buttonText !== "Delete" ? (
            <button onClick={onClick} className={buttonClassName}>
              {buttonText}
            </button>
          ) : null}
        </div>
      </div>
    </div>
  );
};

export default Card;
