import { createSlice, PayloadAction } from '@reduxjs/toolkit'


import type { RootState } from '../store'



export interface IProduct {
    title: string;
    image: string;
    price: number;
    amount: number;


}
const initialState = {
    allProducts: [] as IProduct[],
    productsInCart: [] as IProduct[],
    favItems: [] as IProduct[],
    totalProductCount: 0,




}


export const ShoppingSlice = createSlice({
    name: 'shop',
    initialState,
    reducers: {
        addProducts: (state, action: PayloadAction<IProduct[]>) => {
            if (state.allProducts.length === 0) {
                state.allProducts = action.payload

            }
        },
        addToCart: (state, action: PayloadAction<IProduct>) => {
            let isContain = false;
            state.productsInCart.forEach(element => {
                if (element.title === action.payload.title) {
                    element.amount += 1;
                    isContain = true;
                    state.totalProductCount += 1;
                    return;

                }
            })
            if (isContain === false) {
                const item = action.payload;
                state.productsInCart.push({ title: item.title, image: item.image, price: item.price, amount: 1 } as IProduct);
                state.totalProductCount += 1;
            }

        },
        deleteToCart: (state, action: PayloadAction<IProduct>) => {
            state.productsInCart = state.productsInCart.filter((product) =>
                product.title !== action.payload.title);
        },
        increaseToCart: (state, action: PayloadAction<IProduct>) => {
            state.productsInCart.forEach(element => {
                if (element.title === action.payload.title) {
                    element.amount += 1;
                    state.totalProductCount += 1;
                }
            });
        },
        decreaseToCart: (state, action: PayloadAction<IProduct>) => {
            state.productsInCart.forEach(element => {
                if (element.title === action.payload.title && action.payload.amount > 0)
                    element.amount -= 1;
                state.totalProductCount -= 1;
            });
        },
        favAddToCart: (state, action: PayloadAction<IProduct>) => {
            const item = action.payload
            if (state.favItems.some(item => item.title === action.payload.title)) {
                return;

            }
            else {
                state.favItems.push({ title: item.title, image: item.image, price: item.price, amount: 1 } as IProduct);
            }






        },

    }


}
)

export const { addProducts, addToCart, deleteToCart, increaseToCart, decreaseToCart, favAddToCart } = ShoppingSlice.actions

export const shop = (state: RootState) => state.shop

export default ShoppingSlice.reducer